import { Component, OnInit } from '@angular/core';

import {FormGroup, FormBuilder, Validators, FormControl} from '@angular/forms';
import { registration } from 'src/app/models/registration';
import { UserServiceService } from 'src/app/services/user-service.service';
import { ServiceService } from '../../../services/service.service';
import {formatDate } from '@angular/common';

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit {
  registrationForm: FormGroup;
  jstoday = '';
  constructor(private formBuilder: FormBuilder, private service: UserServiceService) { 
    
  }

  ngOnInit() {
    this.initForm();
  }

  private initForm()
  {
    this.registrationForm = this.formBuilder.group({
      userName: ['', [Validators.required]],
      email:   ['', [Validators.required]],
      mobile:  ['', [Validators.required]],
      password: ['', [Validators.required]],
      confirmPassword: ['', [Validators.required]],
    });
  }

  SubmitRegistration()
  {

   
   var user = this.registrationForm.get('userName').value;
   var reg = new registration(user, 1, this.registrationForm.get('email').value , this.registrationForm.get('mobile').value,
   this.registrationForm.get('password').value,'2020-11-24T17:16:40', user, '2020-11-24T17:16:40'  );

   this.service.insertUser(reg).subscribe({
     next: (response: any) => {
       console.log('log: '+  response);
     }
   });
  }


  


}
