export class registration{
    UserName: string;
    UserType: number;
    Email: string;
    Mobile: string;
    Password: string;
    CreatedOn: string;
    CreatedBy: string;
    UpdatedOn: string;

    constructor(UserName: string, UserType: number, Email: string, Mobile: string, Password: string, CreatedOn: string, CreatedBy: string, UpdatedOn: string){
            this.UserName = UserName;
            this.UserType = UserType;
            this.Email = Email;
            this.Mobile = Mobile;
            this.Password = Password;
            this.CreatedOn = CreatedOn;
            this.CreatedBy = CreatedBy;
            this.UpdatedOn = UpdatedOn;
    }

}